<template>
  <div class="orders_info">
    <div class="orders_info_main">
      <van-cell
        center
        icon-prefix="iconfont iconfahuo"
        icon="iconfahuo"
        label="上海闵行区中药材厂"
        v-show="state===2||state===3"
      >
        <template #title>
          <span class="custom-title">已发货</span>
          <span class="orders_id">订单编号127400274930</span>
        </template>
      </van-cell>
      <van-cell center icon="location" is-link label="上海市闵行区上海康城小区城道80号503室">
        <template #title>
          <span class="custom-title">张杰医师</span>
          <span class="phone">127400274930</span>
        </template>
      </van-cell>
      <div class="cell_info">
        <div class="info_first">
          <div class="info_image">
            <van-image width="100%" height="100%" src="https://img.yzcdn.cn/vant/cat.jpeg" />
          </div>
          <div>
            <div>
              <div>宁夏枸杞</div>
              <div>250/g 一袋</div>
              <div class="time" v-show="state===1" @click="state=2">预计24小时内发货</div>
            </div>
            <div>
              <div>¥19.9</div>
              <div>×1</div>
            </div>
          </div>
        </div>
        <div class="button_t" v-if="false">
          <span v-show="state===0||state===1||state===2">退款</span>
          <span v-show="state===3">申请售后</span>
        </div>
        <div class="item">
          <span>商品总价</span>
          <span>¥19.9</span>
        </div>
        <div class="item">
          <span>运费</span>
          <span>¥00.0</span>
        </div>
        <div class="item">
          <span>实付款</span>
          <span>¥19.9</span>
        </div>
      </div>
      <div class="orders_info_item">
        <div>
          <span></span> 订单信息
        </div>
        <div>
          <span>订单编号：</span>
          <span>202004084723</span>
        </div>
        <div>
          <span>创建时间：</span>
          <span>20200408472314: 20</span>
        </div>
        <div>
          <span>付款时间：</span>
          <span>20200408472315: 00</span>
        </div>
        <div>
          <span>发货编号：</span>
          <span>20200408472315: 00</span>
        </div>
      </div>
    </div>
    <div class="footer_div">
      <div class="button-wrapper" v-show="state===0">
        <span class="grey">取消订单</span>
        <span class="red" @click="state=1">付款</span>
      </div>
      <div class="button-wrapper" v-show="state===2">
        <span class="grey" @click="goPath('logisticsInfo')">查看物流</span>
        <span class="red" @click="state=3">确认收货</span>
      </div>
      <div class="button-wrapper" v-show="state===3">
        <span class="grey" @click="goPath('logisticsInfo')">查看物流</span>
        <span class="red">删除订单</span>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      state: 0,
      value: "",
    };
  },
  methods: {
    goPath(name) {
      this.$router.push({name});
    },
  },
};
</script>
<style lang="scss" scoped>
.orders_info {
  width: 100%;
  height: 100%;
  .orders_info_main {
    width: 100%;
    height: auto;
    padding: 15px 0 60px;
    .van-cell {
      margin-bottom: 15px;
      padding: 10px 12px;
      i:first-child {
        width: 25px;
        height: 25px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
        background-color: #c8a060;
        color: #fff;
        font-size: 12px;
      }
      .van-cell__left-icon {
        margin-right: 12px;
      }
      .van-cell__title {
        .custom-title {
          font-size: 15px;
          color: #000000;
          margin-right: 10px;
        }
        .phone {
          font-size: 12px;
          color: #666666;
        }
        .van-cell__label {
          font-size: 13px;
          color: #000000;
        }
        .orders_id {
          font-size: 12px;
          color: #000000;
        }
      }
    }
    .cell_info {
      padding: 5px 15px 15px;
      width: 100%;
      background-color: #fff;
      > .info_first {
        width: 100%;
        height: 95px;
        display: flex;
        .info_image {
          width: 125px;
          height: 100%;
          border-radius: 5px;
          overflow: hidden;
        }
        > div:last-child {
          flex-grow: 1;
          padding-left: 15px;
          height: 100%;
          display: flex;
          justify-content: space-between;
          align-items: baseline;
          > div:first-child {
            height: 100%;
            color: #000;
            > div {
              font-size: 13px;
              margin-bottom: 12px;
            }
            .time {
              color: #daaf6b;
            }
            > div:first-child {
              font-size: 16px;
            }
          }
          > div:last-child {
            text-align: center;
            > div:first-child {
              font-size: 13px;
              color: #000;
            }
            > div:last-child {
              font-size: 13px;
              margin-top: 2px;
              color: #666666;
            }
          }
        }
      }
      > div {
        display: flex;
        justify-content: space-between;
        margin-top: 10px;
      }
      > .button_t {
        width: 100%;
        display: flex;
        justify-content: flex-end;
        > span {
          border: solid 1px #333;
          border-radius: 25px;
          font-size: 14px;
          width: 74px;
          height: 29px;
          color: #333;
          display: flex;
          justify-content: center;
          align-items: center;
        }
      }
      .item{
          align-items: baseline;
      }
      > div:nth-of-type(3) {
        font-size: 13px;
      }
      > div:nth-of-type(4) {
        font-size: 13px;
      }
      > div:nth-of-type(5) {
        font-size: 16px;
        > span:last-child {
          color: #7e3330;
        }
      }
    }
    .orders_info_item {
      margin-top: 15px;
      padding: 15px 15px 0;
      background-color: #fff;
      > div:first-child {
        display: flex;
        align-items: center;
        font-size: 16px;
        color: #000;
        > span {
          display: inline-block;
          width: 2px;
          height: 18px;
          margin-right: 5px;
          background-color: #c8a060;
        }
      }
      > div {
        padding-bottom: 15px;
        font-size: 13px;
        color: #666;
      }
    }
  }
  .footer_div {
    position: fixed;
    bottom: 0;
    width: 100%;
    border-top: 1px solid rgba(209, 209, 209, 0.5);
    background-color: #fff;
    padding-right: 0;
    .button-wrapper {
      display: flex;
      height: 45px;
      justify-content: flex-end;
      align-items: center;
      span {
        display: inline-flex;
        border-radius: 25px;
        font-size: 14px;
        padding: 4px 8px;
        margin-right: 15px;
      }
      .red {
        color: #7e3330;
        border: solid 1px #7e3330;
      }
      .grey {
        color: #a0a0a0;
        border: solid 1px #a0a0a0;
      }
    }
  }
}
</style>



